h1 {
  text-align: center; 
  color: #D96525; 
  font-family: 'Josefin Sans', sans-serif;
}

form {
  display: flex;
  flex-wrap: wrap;
  font-family: 'Josefin Sans', sans-serif;
  border-radius: 20px; 
  margin: 20px; 
}

input {
  flex: 1 1 10ch;
  margin: 5px;
  border: none;
  background: hsl(0 0% 93%);
  border-radius: .25rem;
  padding: .75rem 1rem;
  font-family: 'Josefin Sans', sans-serif;
  border-radius: 20px; 
}

button {
  background: #D96525;
  color: white;
  box-shadow: 0 .75rem .5rem -.5rem hsl(0 50% 80%);
  font-family: 'Josefin Sans', sans-serif;
  border-radius: 20px; 
  margin: 5px; 
}

button:hover {
  background-color: blue; 
}

.post {
  display: flex; 
  margin: auto; 
  font-size: 2em; 
}

